/* eslint-disable no-param-reassign */
import React, { useState, useEffect } from 'react';
import { StyleSheet, css } from 'aphrodite';
import Order from '../Components/Order';
import { firestore } from '../utils/firebase';

const styles = StyleSheet.create({
  section: {
    backgroundImage: 'linear-gradient(#D3AA62, #BF3904)',
    boxSizing: 'border-box',
    margin: '3% auto',
    padding: '1%',
    borderRadius: '5px',
    width: '95vw',
  },
  article: {
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'wrap',
  },
  title: {
    textAlign: 'center',
    color: '#8D0A0A',
    fontSize: '1.8rem',
    fontWeight: 'bolder',
  },
});

const Kitchen = () => {
  const [order, setOrder] = useState([]);
  const [ready, setReady] = useState([]);
  const [history, setHistory] = useState([]);

  useEffect(() => {
    firestore.collection('orders').where('status', '==', 'em prepara√ß√£o').get()
      .then((snap) => {
        snap.forEach((doc) => (
          setOrder((currency) => [...currency, { ...doc.data(), id: doc.id }])));
      });
    firestore.collection('orders').where('status', '==', 'pronto').get()
      .then((snap) => {
        snap.forEach((doc) => (
          setReady((currency) => [...currency, { ...doc.data(), id: doc.id }])));
      });
    firestore.collection('orders').where('status', '==', 'entregue').get()
      .then((snap) => {
        snap.forEach((doc) => (
          setHistory((currency) => [...currency, { ...doc.data(), id: doc.id }])));
      });
  }, []);

  function Ready(item) {
    const timePast = (new Date().getTime() - item.clock) / 1000;
    const leadTime = `${parseInt(timePast / 3600, 10)}h:${parseInt(timePast / 60, 10)}m:${parseInt(timePast % 60, 10)}s`;
    firestore.collection('orders').doc(item.id).update({
      status: 'pronto',
      leadTime,
    });
    item.leadTime = leadTime;
    item.status = 'pronto';
    setReady([...ready, item]);
    setOrder(order.filter((i) => i.status === 'em prepara√ß√£o'));
  }

  function Archieve(i) {
    firestore.collection('orders').doc(i.id).update({
      status: 'entregue',
    });
    i.status = 'entregue';
    setHistory([...history, i]);
    setReady(order.filter((item) => item.status === 'pronto'));
  }

  function Delete(i) {
    firestore.collection('orders').doc(i.id).delete();
    history.splice(history.indexOf(i), 1);
    setHistory([...history]);
  }

  return (
    <div>
      <section className={css(styles.section)}>
        <h4 className={css(styles.title)}>
          <span role="img" aria-label="Man Cook">üë®‚Äçüç≥</span>
          Pedidos em produ√ß√£o
          <span role="img" aria-label="Man Cook">üë®‚Äçüç≥</span>
        </h4>
        <article className={css(styles.article)}>
          {order.map((i) => (
            <Order
              id={i.id}
              client={i.client}
              table={i.table}
              orderClient={i.order}
              date={i.date}
              nameBtn="Pronto"
              time={i.time}
              status={i.status}
              handleClick={() => Ready(i)}
            />
          ))}
        </article>
      </section>
      <section className={css(styles.section)}>
        <h4 className={css(styles.title)}>
          <span role="img" aria-label="Fork and Knife With Plate">üçΩÔ∏è</span>
          Pedidos prontos
          <span role="img" aria-label="Fork and Knife With Plate">üçΩÔ∏è</span>
        </h4>
        <article className={css(styles.article)}>
          {ready.map((i) => (
            <Order
              client={i.client}
              table={i.table}
              orderClient={i.order}
              date={i.date}
              time={i.time}
              leadTime={i.leadTime}
              nameBtn="Arquivar"
              status={i.status}
              handleClick={() => Archieve(i)}
            />
          ))}
        </article>
      </section>
      <section className={css(styles.section)}>
        <h4 className={css(styles.title)}>
          <span role="img" aria-label="scrool">üìú</span>
            Hist√≥rico de pedidos
          <span role="img" aria-label="scrool">üìú</span>
        </h4>
        <article className={css(styles.article)}>
          {history.map((i) => (
            <Order
              client={i.client}
              table={i.table}
              orderClient={i.order}
              date={i.date}
              time={i.time}
              leadTime={i.leadTime}
              nameBtn="Deletar"
              status={i.status}
              handleClick={() => Delete(i)}
            />
          ))}
        </article>
      </section>
    </div>
  );
};

export default Kitchen;
